.light {
  --bg: var(--white-alt);
  --text: var(--bs-gray-900);
  --gray: var(--bs-gray-900);
  --gray-2: var(--bs-gray-200);
  --nav: var(--white-color);
  --footer: var(--dark-color);
  --fot-text: var(--bs-gray-100);
}
.dark {
  --bg: var(--dark-color);
  --text: var(--bs-gray-100);
  --nav: var(--dark-color-2);
  --gray: var(--bs-gray-400);
  --gray-2: var(--bs-gray-900);
  --footer: var(--white-alt);
  --fot-text: var(--bs-gray-900);
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
p {
  margin-bottom: 0;
}
body {
  font-family: "Open Sans", sans-serif;
  background: var(--bg);
  color: var(--text);
  transition: background 0.5s ease-in-out, color 550ms ease-in-out;
  padding-left: 0;
  margin-top: 70px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
/* Small */
@media (min-width: 768px) {
  .container {
    max-width: 750px;
  }
}
/* Medium */
@media (min-width: 992px) {
  .container {
    max-width: 970px;
  }
}
/* Large */
@media (min-width: 1200px) {
  .container {
    max-width: 1190px;
  }
}
