.slide {
  width: 100px;
  height: 100%;
  background: var(--linear-1);
  color: var(--text);
  z-index: 4;
  position: fixed;
  transition: 0.5s ease-in-out;
  left: -100px;
  &.show {
    left: 0;
  }
  .ul-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    .li-slide-item {
      width: 90px;
      text-align: center;
      margin-top: 80px;
      color: var(--bs-gray-400);
      cursor: pointer;
    }
  }
}
