.main {
  padding-top: var(--section-padding-sm);
  padding-bottom: var(--section-padding-sm);
}
.main-home {
  user-select: none;
  width: calc(100% - 100px);
  background: var(--white);
  color: var(--text);
  margin: auto;
  margin-bottom: 20px;
  position: relative;
}
h1 {
  font: {
    size: 24rem;
    weight: bold;
  }
  text-align: center;
  color: var(--bs-indigo);
}
.text-home {
  position: absolute;
  top: 0;
  left: 55px;
  font: {
    size: 2rem;
  }
  text-indent: 172px;
  font-weight: bold;

  .name {
    color: var(--bs-indigo);
  }
  .work-name {
    display: block;
  }
}
.learnMore {
  padding: 10px 30px;
  background: var(--linear-1);
  color: var(--white-color);
  border: 1px solid var(--bs-indigo-alt);
  border-radius: 6px;
  font-size: 24px;
  margin-top: -20px;
  position: relative;
  left: 229px;
  &:hover {
    background: var(--linear-2);
  }
}
.scrollUp {
  opacity: 0;
  width: 30px;
  height: 30px;
  background: var(--bs-indigo);
  color: var(--linear-1);
  position: fixed;
  right: 40px;
  bottom: 84px;
  border-radius: 6px;
  border: 1px solid var(--bs-cyan);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  z-index: 101;
}
