.skills {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
  overflow: hidden;
  .skill {
    padding: 10px;
    .container {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-wrap: wrap;
      gap: 15px;
      transform: translateX(10px);
      .box {
        width: calc(93% / 4);
        background: var(--nav);
        padding: 0 20px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 25px;
        border-radius: 0.5rem;
        box-shadow: 11px 10px 22px -13px var(--black-color);
        transition: 0.5s ease-in-out;
        height: 125px;
        position: relative;
        transition: 0.5s ease-in-out;
        cursor: pointer;
        margin-top: 5px;
        &:hover {
          transform: translateY(-20px);
        }
        &.html {
          left: -500px;
        }
        &.css {
          bottom: 500px;
        }
        &.bootstrap {
          bottom: 500px;
        }
        &.js {
          right: -500px;
        }
        &.jquery {
          left: -500px;
        }
        &.sass {
          bottom: -500px;
        }
        &.github {
          bottom: -500px;
        }
        &.pug {
          right: -500px;
        }
        &.gulp {
          bottom: -500px;
        }

        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          background: var(--bs-indigo-alt);
          width: 5px;
          height: 0;
          display: block;
          transition: 0.5s ease-in-out;
        }
        &:hover::after {
          height: 100%;
        }
        img {
          width: 60px;
          margin-top: -10px;
        }
        h5 {
          margin-left: 5px;
        }
      }
    }
  }
}
