@media (max-width: 767px) {
  // nav
  .navbar {
    .theme {
      li {
        position: absolute;
        right: 20px;
      }
    }
  }
}
@media (max-width: 767px) {
  // home
  body {
    font-size: 14px;
  }
  .main {
    min-height: 100vh;
  }
  .main-home {
    text-align: center;
    // margin-left: 50px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .text-home {
      font-size: 1.5rem;
      text-indent: 21px;
      position: static;
      text-align: center;
    }
    h1 {
      font-size: 6rem;
      text-align: center;

      margin-left: 20px;
    }

    .learnMore {
      font-size: 16px;
      width: fit-content;
      margin-top: 20px;
      margin-left: 20px;
      left: 0;
    }
  }
  .scrollUp {
    right: 10px;
    bottom: 30px;
  }
}
@media (max-width: 767px) {
  // Slide
  .main-slide {
    width: 40px;
    .slide {
      width: 40px;
    }
    .slide .ul-slide .li-slide-item {
      width: 40px;
    }
  }
}
@media (max-width: 767px) {
  // detalis
  .details {
    padding-top: 20px;
    padding-bottom: 20px;
    margin-left: 15px;
    .title-container {
      width: 100%;
      .cv {
        width: 80%;
        margin: auto;
        text-align: center;
        .contactMe {
          font-size: 16px;
          width: fit-content;
          text-align: center;
        }
      }
      flex-direction: column-reverse;
      .photo-contaienr {
        img {
          width: 200px;
          margin-right: 10px;
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .skills {
    .skill {
      .container {
        .box {
          width: calc(90% / 2);
          text-align: center;
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .skills {
    .skill {
      .container {
        .box {
          width: 100%;
          text-align: center;
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .contact-detils {
    grid-template-columns: repeat(auto-fill, minmax(299px, 1fr)) !important;
    transform: translateX(10px);
  }
  .contact .contact-detils .box {
    height: 110px;
  }
  .contact .contact-detils .box div div {
    padding: 12px;
    margin: 8px 16px 14px 13px;
  }
  .contact .contact-detils .box div {
    justify-content: center;
    font-size: 19px;
  }
  .contact .contact-detils .box div p {
    text-indent: 0;
    text-align: center !important;
    font-size: 16px;
  }
  .contact .container .spinner {
    margin-left: 33px;
  }
  footer {
    font-size: 13px;
  }
}
