.details {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
  margin-left: var(--section-margin);
  .title-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 60px;
    .cv {
      width: 50%;
      h2 {
        font-weight: 800;
        .name {
          color: var(--bs-indigo);
        }
      }
      p {
        margin-top: 20px;
        font-size: 1.5rem;
        font-weight: 600;
      }
      .contactMe {
        margin-top: 25px;
        padding: 10px 30px;
        background: var(--linear-1);
        color: var(--white-color);
        border: 1px solid var(--bs-indigo-alt);
        border-radius: 6px;
        font-size: 24px;
        &:hover {
          background: var(--linear-2);
        }
      }
    }
    .photo-contaienr {
      img {
        width: 300px;
        border-radius: 30px;
        box-shadow: 14px 16px 19px rgb(51 57 64 / 75%);
        margin-right: 70px;
      }
    }
  }
}
