.navbar {
  background: var(--nav);
  color: var(--text);
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--nav);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 55;
  .box {
    width: 50px;
    height: 50px;
    margin-left: 20px;
    margin-top: 16px;
    cursor: pointer;

    span {
      background-color: var(--gray);
      height: 3px;
      display: block;
      margin-top: 5px;
      transition: 0.5s ease-in-out;
    }
    .spn-1 {
      width: 50%;
    }
    .spn-2 {
      width: 75%;
    }
    .spn-3 {
      width: 100%;
    }
    &:hover span {
      width: 100%;
    }
  }
  .theme {
    display: flex;
    align-items: center;
    position: relative;
    li {
      position: absolute;
      top: -20px;
      right: 40px;
    }
    .btn-theme {
      padding: 7px 20px;
      border-radius: 6px;
      background: var(--linear-1);
      border: 1px solid var(--bs-gray-800);
      color: var(--bs-gray-100);
      cursor: pointer;

      &:hover {
        transition: 0.5s ease-in-out;
        background: var(--linear-2);
      }
    }
  }
}
.active {
  display: block;
}
.hide {
  display: none;
}
