.contact {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
  .container {
    .spinner {
      margin-left: 48px;
      width: 135px;
      h2 {
        font-size: 1.3rem;
        margin-left: -5px;
      }
    }
  }
  .contact-detils {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
    gap: 20px;
    justify-content: center;
    padding: 35px 39px;
    .box {
      background: var(--nav);
      border-radius: 0.5rem;
      div {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
        div {
          background: var(--bs-indigo);
          padding: 15px;
          border-radius: 50%;
          margin: 15px 28px 17px 20px;
        }
        h4 {
          width: 70%;
        }
        p {
          font-size: 20px;
          text-indent: 93px;
          margin-bottom: 15px;
        }
      }
    }
  }
}
footer {
  color: #fff;
  background: var(--dark-color-2);
  padding: 20px 0;
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.social {
  padding: 40px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  div {
    /* padding: 10px; */
    border-radius: 9px;
    width: 50px;
    height: 50px;
    line-height: 61px;
    text-align: center;
    background: var(--dark-color-2);
    transition: 0.5s ease-in-out;
    a {
      color: #fff;
      display: block;
    }
    &:hover:nth-child(1) {
      background-color: #1877f2;
    }
    &:hover:nth-child(2) {
      background-color: #075e54;
    }
    &:hover:nth-child(3) {
      background-color: #ffa930;
    }
    &:hover:nth-child(4) {
      background-color: #68217a;
    }
  }
}
