.works {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
  .work_box {
    padding: 0 10px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 10px;
    transform: translateX(30px);
    margin-top: 20px;
    margin-right: 20px;
  }
  .work_item {
    text-align: center;
  }
  .item {
    width: 90%;
    height: 200px;
    background: var(--nav);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
    position: relative;
    overflow: hidden;
    img {
      height: 200px;
      width: 342px;
    }
  }
  .text_div {
    display: flex;
    // align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    transition: 0.5s ease-in-out;
    &:hover {
      background-color: rgba(0, 0, 0, 75%);
    }
    &:hover .text {
      top: 50%;
    }
    position: absolute;
    .text {
      transition: 0.3s ease-in-out;
      position: absolute;
      top: 1000px;
      transform: translateY(-50%);
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      h4 {
        color: #fff;
        width: 100%;
        margin-bottom: 30px;
        text-align: center;
      }
      a {
        background: var(--linear-2);
        font-size: 1rem;
        color: #fff;
        padding: 5px 20px;
        border-radius: 6px;
      }
    }
  }
}
